"use client";

import React, { useState, useEffect } from "react";

interface TransferEntry {
  code: string;
  quantity: number;
  fromStore: string;
  toStore: string;
  name: string;
  date: string;
  confirmed: boolean;
}

const ADMIN_USER = "admin";
const ADMIN_PASS = "password";

export default function AdminPage() {
  const [user, setUser] = useState("");
  const [pass, setPass] = useState("");
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [transferData, setTransferData] = useState<TransferEntry[]>([]);
  const [confirmations, setConfirmations] = useState<{ [key: string]: boolean }>({});

  useEffect(() => {
    // Load transfer data from sessionStorage
    const data = sessionStorage.getItem("transferData");
    if (data) {
      setTransferData(JSON.parse(data));
    }
    // Load confirmations from sessionStorage
    const savedConfirmations = sessionStorage.getItem("confirmations");
    if (savedConfirmations) {
      setConfirmations(JSON.parse(savedConfirmations));
    }
  }, []);

  const handleLogin = () => {
    if (user === ADMIN_USER && pass === ADMIN_PASS) {
      setIsAuthenticated(true);
    } else {
      alert("Invalid username or password");
    }
  };

  const toggleConfirmation = async (key: string) => {
    const newConfirmations = { ...confirmations, [key]: !confirmations[key] };
    setConfirmations(newConfirmations);
    try {
      const res = await fetch('/api/confirmations', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(newConfirmations),
      });
      if (!res.ok) throw new Error('Failed to update confirmations');
      // Update local state already done
    } catch (error) {
      alert("Error updating confirmations: " + (error instanceof Error ? error.message : String(error)));
    }
  };

  if (!isAuthenticated) {
    return (
      <main className="min-h-screen bg-white text-black flex flex-col items-center justify-center p-6 font-sans">
        <h1 className="text-3xl font-bold mb-6">Admin Login</h1>
        <div className="w-full max-w-sm space-y-4">
          <input
            type="text"
            placeholder="Username"
            value={user}
            onChange={(e) => setUser(e.target.value)}
            className="w-full border border-black rounded px-3 py-2"
          />
          <input
            type="password"
            placeholder="Password"
            value={pass}
            onChange={(e) => setPass(e.target.value)}
            className="w-full border border-black rounded px-3 py-2"
          />
          <button
            onClick={handleLogin}
            className="w-full bg-black text-white py-2 rounded font-semibold hover:bg-gray-800 transition"
          >
            Login
          </button>
        </div>
      </main>
    );
  }

  // Group entries by name and date
  const groupedEntries: { [key: string]: TransferEntry[] } = {};
  transferData.forEach((entry) => {
    const key = `${entry.name}-${entry.date}`;
    if (!groupedEntries[key]) {
      groupedEntries[key] = [];
    }
    groupedEntries[key].push(entry);
  });

  return (
    <main className="min-h-screen bg-white text-black p-6 font-sans">
      <h1 className="text-3xl font-bold mb-6">Admin Panel - Transfer Requests</h1>
      {transferData.length === 0 ? (
        <p>No transfer requests found.</p>
      ) : (
        Object.entries(groupedEntries).map(([key, entries]) => {
          const [name, date] = key.split("-");
          const confirmed = confirmations[key] || false;
          return (
            <div key={key} className="mb-6 border border-black rounded p-4">
              <div className="flex justify-between items-center mb-2">
                <div>
                  <p className="font-semibold">Name: {name}</p>
                  <p className="text-sm text-gray-600">Date: {new Date(date).toLocaleString()}</p>
                </div>
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={confirmed}
                    onChange={() => toggleConfirmation(key)}
                    className="w-5 h-5"
                  />
                  <span>Confirmed</span>
                </label>
              </div>
              <table className="w-full border border-black rounded table-auto">
                <thead>
                  <tr className="border-b border-black">
                    <th className="px-4 py-2 text-left">Code</th>
                    <th className="px-4 py-2 text-left">Quantity</th>
                    <th className="px-4 py-2 text-left">From Store</th>
                    <th className="px-4 py-2 text-left">To Store</th>
                  </tr>
                </thead>
                <tbody>
                  {entries.map((entry, idx) => (
                    <tr key={idx} className="border-b border-black">
                      <td className="px-4 py-2">{entry.code}</td>
                      <td className="px-4 py-2">{entry.quantity}</td>
                      <td className="px-4 py-2">{entry.fromStore}</td>
                      <td className="px-4 py-2">{entry.toStore}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          );
        })
      )}
    </main>
  );
}
